import React, { useState } from "react";
import { Container, Button, Alert, Nav, Row, Card } from "react-bootstrap";
import { useAuth } from "../contexts/AuthContext";
import { Link, useHistory } from "react-router-dom";

export const Dashboard = () => {
  const [error, setError] = useState("");
  const { currentUser, logout } = useAuth();
  const history = useHistory();

  async function handleLogout() {
    setError("");

    try {
      await logout();
      history.push("/login");
    } catch {
      setError("Failed to log out");
    }
  }

  return (
    <>
      <Container>
        <Nav style={{ display: "flex", alignItems: "center" }}>
          <h2 className="text-center mb-4 ">Profile</h2>
          <div style={{ flex: 1 }}>Email: {currentUser.email}</div>
          <div className="text-center mt-2">
            <Button variant="link" onClick={handleLogout}>
              Log Out
            </Button>
          </div>
        </Nav>
      </Container>
      {error && <Alert variant="danger">{error}</Alert>}

      <Row>
        <Card style={{ width: "18rem" }}>
          <Card.Img
            variant="top"
            src="https://www.plotprojects.com/wp-content/uploads/2014/06/multiple-users.png"
          />
          <Card.Body>
            <Card.Title>Users</Card.Title>
            <Card.Text>All existing users</Card.Text>
            <Link className="btn btn-primary" to={"/users/list"}>
              List
            </Link>
          </Card.Body>
        </Card>
        <Card style={{ width: "18rem" }}>
          <Card.Img
            variant="top"
            src="https://cdn1.iconfinder.com/data/icons/avatar-1-2/512/Add_User1-512.png"
          />
          <Card.Body>
            <Card.Title>Create User</Card.Title>
            <Card.Text>Create new user</Card.Text>
            <Link className="btn btn-primary" to={"/users/new"}>
              Create user
            </Link>
          </Card.Body>
        </Card>
      </Row>
    </>
  );
};
